@import '../common/common.css';

.Home-layout {
  width: 100%;
}

.Home-banner {
  width: 100%;
}

.Home-banner-img {
  filter: brightness(70%);
  width: 100%;
  height: auto;
  z-index: -1;
  mask-image: linear-gradient(to top, rgba(7, 14, 18, 0) 0%, rgba(7, 14, 18, 0.85) 100%);
  filter: blur(5px);

  opacity: 0;
  animation-duration: 3s;
  animation-name: fadein;
  animation-timing-function: ease-out;
  animation-fill-mode: forwards;
}

.Home-banner-caption {
  top: 10vmin;
  left: 0px;
  position: absolute;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  color: rgba(255, 255, 255, 0.8);
  height: 100vmin;
  width: 100%;
}

.Home-banner-caption-h0 {
  display: flex;
  align-items: center;

  flex: 2;
  font-size: 6vw;
  position: relative;
  opacity: 0;
  font-weight: bolder;
  text-shadow: #FFFFFF 0px 0px 10px;

  animation-duration: 1s;
  animation-name: fadein, slideup;
  animation-fill-mode: forwards;
  animation-delay: 0.5s;

  transition-duration: 150ms;
  transition-timing-function: ease-out;
}

.Home-banner-caption-h0:hover {
  text-shadow: #FFFFFF 0px 0px 50px;
}

.Home-banner-caption-h1 {
  display: flex;
  align-items: center;

  flex: 1;
  font-size: 5vw;
  position: relative;
  opacity: 0;
  user-select: none;

  animation-duration: 1s;
  animation-name: fadein, slideup;
  animation-delay: 1.25s;
  animation-fill-mode: forwards;
}

.Home-banner-caption-h2 {
  display: flex;
  align-items: center;

  flex: 1;
  font-size: 4vw;
  position: relative;
  opacity: 0;

  animation-duration: 1s;
  animation-name: fadein, slideup;
  animation-delay: 1.5s;
  animation-fill-mode: forwards;
}

.Home-banner-caption-btn {
  flex: 2;
  width: 15vmax;

  margin-bottom: 5vmin;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  position: relative;
  opacity: 0;

  animation-duration: 1s;
  animation-name: fadein, slideup;
  animation-delay: 1.75s;
  animation-fill-mode: forwards;
}

.Home-banner-caption-btn .btn-lg {
  font-size: 2vw;
  border-width: 3px;
}

.Home-partners {
  margin-top: 20vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  color: white;

  transition-duration: 150ms;
  transition-timing-function: ease-out;
}

.Home-partners-caption {
  font-size: 5vw;
  font-weight: bold;
  margin-bottom: 5vh;

  transition-duration: 150ms;
  transition-timing-function: ease-out;
}
